"use client";

import { useState } from "react"; // Removed unused useEffect
import { useSearchParams, useRouter } from "next/navigation"; // Added useRouter for Back button
import MatrixBackground from "../components/MatrixBackground";

// Mock movie data (replace with API or sentiment analysis later)
const mockMovies = [
  { id: 1, title: "Inception", year: 2010, language: "English", genres: ["Action", "Sci-Fi", "Thriller"], poster: "https://via.placeholder.com/200x300", description: "A thief enters dreams to steal secrets." },
  { id: 2, title: "The Shawshank Redemption", year: 1994, language: "English", genres: ["Drama"], poster: "https://via.placeholder.com/200x300", description: "Hope and friendship in prison." },
  { id: 3, title: "Parasite", year: 2019, language: "Korean", genres: ["Drama", "Thriller"], poster: "https://via.placeholder.com/200x300", description: "A family cons their way into wealth." },
  { id: 4, title: "Spirited Away", year: 2001, language: "Japanese", genres: ["Animation", "Fantasy"], poster: "https://via.placeholder.com/200x300", description: "A girl navigates a magical world." },
];

export default function MovieRecommendation() {
  const searchParams = useSearchParams();
  const router = useRouter(); // Added for Back button
  const year = parseInt(searchParams.get("year")) || null; // Changed default to null for better validation
  const language = searchParams.get("language") || "";
  const genres = searchParams.get("genres")?.split(",") || [];
  const [currentMovieIndex, setCurrentMovieIndex] = useState(0);

  // Filter movies based on parameters
  const filteredMovies = mockMovies.filter(
    (movie) =>
      (!year || movie.year >= year) && // Allow null year to show all movies
      (!language || movie.language === language) &&
      (!genres.length || genres.every((genre) => movie.genres.includes(genre)))
  );

  const currentMovie = filteredMovies[currentMovieIndex] || null;

  const handleNextMovie = () => {
    setCurrentMovieIndex((prev) => (prev + 1) % filteredMovies.length || 0);
  };

  const handleBack = () => {
  router.push(`/language-mood`); // Or with params if needed
  };

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-gradient-to-b from-black to-gray-900 text-white font-sans relative">
      <MatrixBackground />
      <h1 className="text-6xl font-extrabold mb-8 text-white tracking-wider drop-shadow-[0_0_10px_rgba(34,197,94,0.7)] animate-pulse z-10" style={{ fontFamily: "'Cinzel', serif" }}>
        Rec&apos;One
      </h1>
      <p className="text-lg mb-6 text-gray-300 italic z-10">Your movie recommendation</p>
      <div className="text-center z-10">
        {currentMovie ? (
          <div className="max-w-md mx-auto">
            <img src={currentMovie.poster} alt={currentMovie.title} className="w-full h-auto rounded-lg mb-4" />
            <h2 className="text-2xl font-semibold text-gray-200">{currentMovie.title} ({currentMovie.year})</h2>
            <p className="text-gray-300 mb-2">Language: {currentMovie.language}</p>
            <p className="text-gray-300 mb-2">Genres: {currentMovie.genres.join(", ")}</p>
            <p className="text-gray-400 mb-4">{currentMovie.description}</p>
            <button
              onClick={handleNextMovie}
              className="px-6 py-2 bg-green-500 text-black font-semibold rounded-lg hover:bg-green-600 transition duration-300 transform hover:scale-105"
              aria-label="Get next movie recommendation"
            >
              Rec&apos;One
            </button>
            <button
              onClick={handleBack}
              className="ml-4 px-6 py-2 bg-gray-500 text-white rounded-lg hover:bg-gray-600 transition duration-300"
              aria-label="Return to genre selection"
            >
              Back
            </button>
          </div>
        ) : (
          <p className="text-gray-400">No movies found matching your criteria.</p>
        )}
      </div>
      <div className="absolute bottom-4 text-gray-400 text-sm opacity-70 z-10">Powered by subreddit sentiment analysis</div>
    </div>
  );
}